/** Media Queries **/
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  color: #2d3137;
}
body::-webkit-scrollbar {
  background-color: rgba(232, 238, 243, 0.8549019608);
  width: 0.8rem;
}
body::-webkit-scrollbar-thumb {
  border-radius: 1rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-m {
  width: 95%;
  max-width: 800px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.2rem;
}

h3 {
  font-size: 1.8rem;
  text-align: center;
}

p {
  margin: 0;
  font-size: 1.6rem;
}

.header {
  padding: 5rem 1rem 10rem 1rem;
  background-color: #5171c3;
  color: #ffffff;
}
.header h1 {
  margin-bottom: 3rem;
}

.budget-form {
  background-color: #ffffff;
  border-radius: 0.5rem;
  padding: 5rem 3rem;
  box-shadow: 5px 5px 15px 5px hsla(210deg, 4%, 11%, 0.15);
  font-weight: 500;
  color: #4062BB;
  position: absolute;
  left: 0;
  right: 0;
  top: 15rem;
}
@media (min-width: 768px) {
  .budget-form {
    padding: 5rem 10rem;
  }
}
.budget-form .field {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
}
.budget-form .field label {
  text-align: center;
  font-size: 1.8rem;
  margin-bottom: 5rem;
}
.budget-form .field input:not([type=submit]) {
  padding: 1rem 0;
  line-height: 2rem;
  border: none;
  border-bottom: solid 2px #4062BB;
  outline: none;
  color: #5171c3;
}
.budget-form .field input:not([type=submit])::placeholder {
  color: #b0bee4;
}
.budget-form .button-budget {
  border: none;
  border-radius: 0.5rem;
  display: "block";
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 500;
  text-align: center;
  background-color: #4062BB;
  color: #ffffff;
  transition: 0.3s;
  width: 100%;
}
.budget-form .button-budget:hover {
  background-color: #334e95;
  cursor: pointer;
}